.SearchedResults {
  border: 1px solid #000000;
  border-top: none;
  border-bottom: none;
  padding: 30px 50px;
  &-items {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    margin: 0 -1.5%;
  }
  &__title {
    font-size: 28px;
    font-weight: bold;
    text-align: left;
  }
}
