.User {
  display: flex;
  justify-content: center;
  align-items: center;
  &__logo {
    width: 42px;
    height: 45px;
  }
  &__options {
    background: url("./opt-triangle.png") no-repeat center center;
    border: none;
    outline: none;
    cursor: pointer;
    width: 18px;
    height: 29px;
    margin-top: 12px;
  }
  &__options:active {
    transform: rotate(90deg);
    transition: 0.3s;
  }
  h2 {
    padding: 0 8px 0 20px;
    font-size: 24px;
    font-weight: normal;
  }
}
