.Pages {
  border: 1px solid #000000;
  border-top: none;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  .prev,
  .next {
    font-size: 20px;
    margin-bottom: 5px;
  }
  .page {
    border: none;
    background: none;
    outline: none;
    cursor: pointer;
  }
  .current {
    font-weight: bold;
  }
}
