.SearchedResults-item {
  margin: 1.5%;
  width: 22%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: left;
  &__poster {
    max-width: 190px;
    max-height: 240px;
  }
  &__title,
  &__type,
  &__year,
  &__id {
    width: 70%;
    margin-top: 5px;
  }
}

@media screen and (max-width: 840px) {
  .SearchedResults-item {
    width: 30%;
  }
}
@media screen and (max-width: 668px) {
  .SearchedResults-item {
    width: 45%;
  }
}

@media screen and (max-width: 500px) {
  .SearchedResults-item {
    width: 85%;
    text-align: center;
  }
}
