.SearchBar {
  margin: 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  flex-grow: 2;
  input {
    width: 90%;
    margin-top: 5px;
  }
  .searchButton {
    margin-top: 8px;
    width: 40px;
    height: 40px;
    border: none;
    outline: none;
    background: none;
    cursor: pointer;
    img {
      width: 20px;
      height: 20px;
    }
  }
  .searchButton:active img {
    transform: scale(0.8);
  }
}
